// import $ from 'jquery';

var hexGrid = function(){
	return {
		grid: function(){
			let width = $('.hexGrid').width()
			let styleBox = $('.hex-style')
			let hexList = $('.hex-container')
			let hexListLength = hexList.length
			let k = 0;
			console.log(hexList, length)
			console.log(width)
			let hexBox = $('.hex-container').width();
			// let hexBox = 185;
			console.log(hexBox)
			if (width > (hexBox*6)) {
				console.log('width <= 1110 && width > 925')
				console.log('--this')
				let k = 6
				// hexList.css('width', width/6.1)
				for (var i = 0; i < hexListLength; i++) {
					$(hexList[i*k]).find('.hex').css({"transform": "translate(0, 0)"})
					$(hexList[i*k + 1]).find('.hex').css({"transform": "translate(-25%, 50%"})
					$(hexList[i*k + 2]).find('.hex').css({"transform": "translate(-50%, 0)"})
					$(hexList[i*k + 3]).find('.hex').css({"transform": "translate(-75%, 50%)"})
					$(hexList[i*k + 4]).find('.hex').css({"transform": "translate(-100%, 0)"})
					$(hexList[i*k + 5]).find('.hex').css({"transform": "translate(-125%, 50%)"})
				}
			}
			if (width <= (hexBox*6) && width > (hexBox*5) ) {
				console.log('width <= 1110 && width > 925')
				console.log('--this')
				// hexList.css('width', width/5.1)
				let k = 5
				for (var i = 0; i < hexListLength; i++) {
					$(hexList[i*k]).find('.hex').css({"transform": "translate(0, 0)"})
					$(hexList[i*k + 1]).find('.hex').css({"transform": "translate(-25%, 50%"})
					$(hexList[i*k + 2]).find('.hex').css({"transform": "translate(-50%, 0)"})
					$(hexList[i*k + 3]).find('.hex').css({"transform": "translate(-75%, 50%)"})
					$(hexList[i*k + 4]).find('.hex').css({"transform": "translate(-100%, 0)"})
				}
			}
			if (width <= (hexBox*5) && width > (hexBox*4) ) {
				console.log('width <= 925 && width > 740')
				// hexList.css('width', width/4.1)
				let k = 4
				for (var i = 0; i < hexListLength; i++) {
					$(hexList[i*k]).find('.hex').css({"transform": "translate(0, 0)"})
					$(hexList[i*k + 1]).find('.hex').css({"transform": "translate(-25%, 50%"})
					$(hexList[i*k + 2]).find('.hex').css({"transform": "translate(-50%, 0)"})
					$(hexList[i*k + 3]).find('.hex').css({"transform": "translate(-75%, 50%)"})
				}
			}
			if (width <= (hexBox*4) && width > (hexBox*3) ) {
				console.log('width <= 740 && width > 555')
				// hexList.css('width', width/3.1)
				let k = 3
				for (var i = 0; i < hexListLength; i++) {
					$(hexList[i*k]).find('.hex').css({"transform": "translate(0, 0)"})
					$(hexList[i*k + 1]).find('.hex').css({"transform": "translate(-25%, 50%"})
					$(hexList[i*k + 2]).find('.hex').css({"transform": "translate(-50%, 0)"})
				}
			}
			if (width <= (hexBox*3) && width > (hexBox*2) ) {
				console.log('width <= 555 && width > 370')
				// hexList.css('width', width/2.1)
				let k = 2
				for (var i = 0; i < hexListLength; i++) {
					$(hexList[i*k]).find('.hex').css({"transform": "translate(0, 0)"})
					$(hexList[i*k + 1]).find('.hex').css({"transform": "translate(-25%, 50%"})
				}
			}
			if (width <= (hexBox*2)) {
				console.log('width <= 555 && width > 370')
				let k = 1
				for (var i = 0; i < hexListLength; i++) {
					$(hexList[i*k]).find('.hex').css({"transform": "translate(0, 0)"})
				}
			}
		},
		resize: function(){
			let self = this
			console.log('---init resize')
			let containerSize  = $('.hexGrid').width()
			$(window).resize(function(){
				// console.log('resize')
				// self.grid();
				$(window).trigger('hexGrid')
			})
		},
		gridInit: function(){
			this.grid();
			$(window).on('hexGrid', () => {
				this.grid()
			})
		},
		initialState(){
			// this.grid()
		},
		init: function(){
			this.gridInit();
			this.resize();
			// this.showGrid();
		}
	}
}

export default hexGrid



